.hero-btn {
  width: 159px;
  height: 44px;
  cursor: pointer;
  color: #d41443;
  background: #fff;
  border: none;
  border-radius: 22px;
  padding-right: 20px;
  font-weight: 500;
  transition: all .25s;
  top: 40px;
  left: 1025px;
}

.hero-btn:after, .hero-btn:before {
  content: "";
  width: 9px;
  height: 2px;
  background-color: #d41443;
  border-radius: 50px;
  position: absolute;
  top: 60px;
}

.hero-btn:after {
  transform: translate(10px)rotate(38deg);
}

.hero-btn:before {
  transform: translate(74px, 5px)rotate(-36deg);
}

.hero-btn:hover {
  background: #ecebeb;
}

